<template>
  <div class="shop-header-container">
    <div class="shop-header">
      <p class="header-title">
        thuycoishop
      </p>
      <div class="contact-icon" @click="openMessenger">
        <img class="contact-icon-img" src="../../assets/contact-icon.png" alt="Liên Hệ"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  methods: {
    openMessenger () {
      window.open('http://m.me/trantiendangkhoa0710', '_blank')
    }
  }
}
</script>

<style scoped>
.contact-icon {
  position: absolute;
  right: 15px;
  border: 1px solid #2c3e50;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.contact-icon-img {
  height: 35px;
  width: 40px;
}
.shop-header-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 65px;
  z-index: 10;
  background-color: white;
  border-bottom: 1px solid #f5f5f5;
}
.shop-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.header-title {
  text-align: center;
  font-size: 20px;
  color: rgba(0, 0, 0, 1);
}
</style>



